*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
 font-size: 20px;
 font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body{
  background-color: #111111;
}

.App{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}

.calc{
  width: 30%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 5px;
}

.key{
  border-radius: 50%;
  background-color: rgb(56, 56, 56);
  color: white;
  display: grid;
  place-items: center;
  aspect-ratio: 1/1;
  font-size: 2.5rem;
  cursor: pointer;
}

.invis{
  opacity: 0;
  pointer-events: none;
}

.grey{
  background-color: grey;
  color: black;
}

.yel{
  background-color: #dfae46;
}


.screen{
  color: white;
  font-size: 4rem;
}


@media (max-width: 1000px){
  .calc{
    width: 50%;
  }
}
@media (max-width: 700px){
  .calc{
    width: 70%;
  }
  html{
    font-size: 16px;
  }
}
@media (max-width: 400px){
  .calc{
    width: 90%;
  }
  html{
    font-size: 16px;
  }
}